import { useCallback, useEffect, useRef, useState } from "react";

export default function useOtp(length, onSubmit) {
  const [otp, setOtp] = useState(new Array(length).fill(""));
  const refs = useRef([]);

  useEffect(() => {
    refs.current = refs.current?.slice(0, length);
  }, [length]);

  const handleChange = (index, value) => {
    if (!/^\d$/.test(value)) return; // Only allow single digits

    setOtp((prevOtp) => {
      const newOtp = [...prevOtp];
      newOtp[index] = value;

      // Focus on the next input
      if (value !== "" && index < length - 1) {
        refs.current[index + 1].focus();
      }

      return newOtp;
    });
  };

  const handlePaste = (index, pastedData) => {
    const newOtp = [...otp];
    pastedData.split("").forEach((value, i) => {
      if (/^\d$/.test(value) && index + i < length) {
        newOtp[index + i] = value;
      }
    });
    setOtp(newOtp);
  };

  const handleKeyDown = (index, event) => {
    if (event.key === "Backspace") {
      event.preventDefault();

      setOtp((prevOtp) => {
        const newOtp = [...prevOtp];

        // Clear current input and focus on the previous one
        if (otp[index] === "") {
          if (index > 0) {
            newOtp[index - 1] = "";
            refs.current[index - 1].focus();
          }
        } else {
          newOtp[index] = "";
        }

        return newOtp;
      });
    }
  };

  const handleSubmit = useCallback(
    (e) => {
      e.preventDefault();
      if (otp.every((code) => code !== "")) {
        onSubmit(otp.join(""));
      } else return null; // OTP is not fully filled
    },
    [otp, onSubmit]
  );

  return { otp, refs, handleChange, handlePaste, handleKeyDown, handleSubmit };
}
