import { sendOtp, verifyUser } from "../../api/userApis";
import Button from "../../components/Button";
import { useModal } from "../../contexts/modalContext";
import { useToast } from "../../contexts/toastContext";
import useOtp from "../../hooks/useOtp";

const EmailOtp = ({ formData, length = 4 }) => {
  const { closeModal } = useModal();
  const toast = useToast();

  const onSubmitHandler = async (otp) => {
    try {
      const response = await verifyUser({
        otp,
        email: formData.email,
      });

      if (response?.status) {
        console.log(response.data);
        closeModal();
      }
    } catch (error) {
      if (!error?.response) {
        toast.error("no server response");
      } else if (error.response) {
        toast.error(error.response?.data?.message);
      } else {
        toast.error("Server issues!!!");
      }
    }
  };

  const handleSendMail = async () => {
    try {
      const response = await sendOtp(formData.email);
      if (response?.status) {
        toast.success("OTP resend successfully");
      }
    } catch (error) {
      if (!error?.response) {
        toast.error("no server response");
      } else if (error.response) {
        toast.error(error.response?.data?.message);
      } else {
        toast.error("Server issues!!!");
      }
    }
  };

  const { otp, refs, handleChange, handlePaste, handleKeyDown, handleSubmit } =
    useOtp(length, onSubmitHandler);

  const onPasteHandler = (e, index) => {
    e.preventDefault();
    const pastedData = e.clipboardData
      .getData("text/plain")
      .substr(0, length - index);
    handlePaste(index, pastedData);
  };

  return (
    <>
      <h2 className="capitalize text-2xl font-semibold text-center mt-8 mb-24">
        Email Verification
      </h2>

      <form onSubmit={handleSubmit}>
        <div className="FORM-FIELD flex flex-col w-max mx-auto my-10">
          <label htmlFor="" className="capitalize font-medium text-center my-4">
            enter otp
          </label>

          <div className="flex gap-6">
            {Array.from({ length }).map((_, index) => (
              <input
                key={index}
                type="text"
                ref={(elem) => (refs.current[index] = elem)}
                maxLength={1}
                value={otp[index]}
                onChange={(e) => handleChange(index, e.target.value)}
                onPaste={(e) => onPasteHandler(e, index)}
                onKeyDown={(e) => handleKeyDown(index, e)}
                className="border border-primary bg-offWhite outline-none size-10 rounded-full text-center"
              />
            ))}
          </div>
        </div>

        <div>
          <p className="text-center text-xs text-slate-400 mb-10">
            If you didn't receive a code.{" "}
            <span
              onClick={handleSendMail}
              className="text-primeRed cursor-pointer hover:underline"
            >
              Resend
            </span>
          </p>
        </div>

        <Button className="w-full py-2 px-6 rounded-full font-secondary">
          Verify
        </Button>
      </form>
    </>
  );
};

export default EmailOtp;
