import { sendOtp } from "../../api/userApis";
import Button from "../../components/Button";
import InputBox from "../../components/InputBox";
import { useModal } from "../../contexts/modalContext";
import { useToast } from "../../contexts/toastContext";
import { useUser } from "../../contexts/userContext";
import useMultiStepForm from "../../hooks/useMultiStepForm";
import { isEmailValid } from "../../utils/validation";
import EmailOtp from "./EmailOtp";
import { IoMdClose } from "react-icons/io";

const EditEmail = () => {
  const { user } = useUser();
  const toast = useToast();
  const { closeModal } = useModal();

  const onSubmitHandler = async (formData) => {
    try {
      const { email } = formData;

      // Client side validations
      if (!isEmailValid(email).success) {
        toast.error("Enter a valid email");
        return;
      }

      const response = await sendOtp(email);

      if (response?.status) {
        goToNextStep();
      }
    } catch (error) {
      console.log(error);
      if (!error?.response) {
        toast.error("no server response");
      } else if (error.response) {
        toast.error(error.response?.data?.message);
      } else {
        toast.error("Server issues!!!");
      }
    }
  };

  const { formData, currentStep, goToNextStep, handleChange, handleSubmit } =
    useMultiStepForm({ email: user.email }, onSubmitHandler, [1, 2]);

  return (
    <div className="size-[520px] max-sm:w-svw max-sm:h-svh shadow-primary bg-white sm:border sm:border-primary sm:rounded-3xl p-10 max-sm:p-6">
      {currentStep === 1 && (
        <>
          <h2 className="capitalize text-2xl font-semibold text-center mt-8 mb-24">
            edit your email
          </h2>

          <form onSubmit={handleSubmit}>
            <InputBox
              label="Enter email address"
              name="email"
              type="email"
              value={formData.email}
              onChange={handleChange}
              validation={isEmailValid}
              align
            />

            <Button className="w-full py-2 px-6 rounded-full font-secondary mt-16">
              update
            </Button>
          </form>
        </>
      )}
      {currentStep === 2 && <EmailOtp formData={formData} />}

      <button
        onClick={closeModal}
        className="absolute top-4 right-4 border border-primary rounded-full p-1"
      >
        <IoMdClose size={24} className="text-primary" />
      </button>
    </div>
  );
};

export default EditEmail;
