import Button from "../../components/Button";
import { useUser } from "../../contexts/userContext";
import { motion } from "framer-motion";
import InputBox from "../../components/InputBox";
import RadioInput from "../../components/RadioInput";
import useForm from "../../hooks/useForm";
import {
  isEmailValid,
  isNameValid,
  isPhoneValid,
} from "../../utils/validation";
import { updateUserDetails } from "../../api/userApis";
import { useState } from "react";
import FileInputBox from "../../components/FileInputBox";
import { MdEdit, MdOutlineEdit } from "react-icons/md";
import { useToast } from "../../contexts/toastContext";
import { useModal } from "../../contexts/modalContext";
import EditEmail from "./EditEmail";

const Profile = () => {
  const [previewMode, setPreviewMode] = useState(true);
  const [selectedAvatar, setSelectedAvatar] = useState(null);
  const { openModal } = useModal();
  const toast = useToast();
  const { user } = useUser();

  const handleFileChange = (event) => {
    const avatar = event.target.files[0];
    if (!avatar) {
      setSelectedAvatar(null);
    } else {
      setSelectedAvatar(URL.createObjectURL(avatar));
    }
    handleChange(event);
  };

  const onSubmitHandler = async (userData) => {
    try {
      const { fullName, gender } = userData;
      const files = userData.avatar;
      let avatar = null;
      if (files) avatar = files[0];

      // Client side validations
      if (!isNameValid(fullName).success) {
        console.log(isNameValid(fullName).message);
        return;
      }

      const response = await updateUserDetails({ fullName, gender, avatar });

      if (response?.status) {
        console.log(response.data);
        toast.success("User data updated successfully");
        setPreviewMode(true);
      }
    } catch (error) {
      if (!error?.response) {
        console.log("no server response");
      } else if (error.response) {
        console.log(error.response?.data?.message);
      } else {
        console.log("Server issues!!!");
      }
    }
  };

  const { formData, handleChange, handleSubmit } = useForm(
    {
      fullName: user.fullName,
      phone: user.phone,
      email: user.email,
      gender: user.gender,
    },
    onSubmitHandler
  );

  return (
    <motion.main
      className="flex-1"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
    >
      <div className="bg-white p-6 rounded-md lg:shadow-md">
        <h2 className="text-2xl font-semibold mb-4">Personal Information</h2>

        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="USER-AVATAR relative size-24 rounded-full mx-auto">
            <img
              src={selectedAvatar || user.imgUrl}
              alt={user.fullName}
              loading="lazy"
              className="size-full rounded-full object-cover object-center"
            />
            {!previewMode && (
              <div className="absolute bottom-0 right-0 rounded-full p-1 bg-white">
                <FileInputBox
                  name="avatar"
                  onChange={handleFileChange}
                  className="p-1 bg-success rounded-full"
                >
                  <MdOutlineEdit className="text-xs text-white" />
                </FileInputBox>
              </div>
            )}
          </div>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <InputBox
              label="name"
              name="fullName"
              value={formData.fullName}
              onChange={handleChange}
              validation={isNameValid}
              readOnly={previewMode}
            />
            {/* <InputBox
              label="last name"
              name="lastName"
              value={formData.lastName}
              onChange={handleChange}
              readOnly={previewMode}
            /> */}
          </div>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="w-full flex items-end gap-x-2">
              <InputBox
                type="email"
                label="email"
                name="email"
                value={formData.email}
                onChange={handleChange}
                validation={isEmailValid}
                readOnly
                containerClassName="flex-col gap-2 flex-grow"
              />
              <Button
                type="button"
                reverse
                onClick={() => openModal(<EditEmail />)}
                className="size-[34px] flex items-center justify-center rounded-md"
              >
                <MdEdit />
              </Button>
            </div>
            <div className="flex items-end gap-x-2">
              <InputBox
                type="tel"
                label="phone"
                name="phone"
                value={formData.phone}
                onChange={handleChange}
                validation={isPhoneValid}
                readOnly
                containerClassName="flex-col gap-2 flex-grow"
              />
              <Button
                type="button"
                reverse
                className="size-[34px] flex items-center justify-center rounded-md"
              >
                <MdEdit />
              </Button>
            </div>
          </div>
          <div className="flex space-x-4">
            <p className="text-sm font-medium">Gender</p>
            <RadioInput
              label="male"
              name="gender"
              value="male"
              checked={formData.gender === "male"}
              onChange={handleChange}
              disabled={previewMode}
            />
            <RadioInput
              label="female"
              name="gender"
              value="female"
              checked={formData.gender === "female"}
              onChange={handleChange}
              disabled={previewMode}
            />
            <RadioInput
              label="others"
              name="gender"
              value="others"
              checked={formData.gender === "others"}
              onChange={handleChange}
              disabled={previewMode}
            />
          </div>
          <div className="BUTTON-SECTION mt-6 flex space-x-4">
            {previewMode ? (
              <button
                type="button"
                onClick={() => setPreviewMode(false)}
                className="px-4 py-2 rounded-md bg-warning text-sm font-medium border border-transparent"
              >
                Edit
              </button>
            ) : (
              <>
                <Button>save</Button>
                <Button
                  type="button"
                  onClick={() => setPreviewMode(true)}
                  reverse
                >
                  cancel
                </Button>
              </>
            )}
          </div>
        </form>
      </div>
    </motion.main>
  );
};

export default Profile;
